/*
AuthProvider

  Authfirebaseauth(a)(u)
  dexie(p)3

  authState               a  u  p                         handler
  ----------------------------------------------------------------------------
  init                    -- -- --  
  disconnected            NG -- --  firebase
  connected               G  -- --  firebase
  
  SignDialog:open          G  NG --            authStateChange
  signedIn                G  G  --        authStateChange
  SignDialog:waiting                     
  
  UserSettingsDialog:open  G  G  NG      userPropChange
  waitingSetting                    
  ready                   G  G  G   a,u,p         userPropChange
  ----------------------------------------------------------------------------

  user: {
    email
  }

  firestore
  userProps: {
    displayName,
    backgroundColor,
    avatarDir,
  }

*/

import React, {useEffect, useReducer, useRef, createContext} from 'react';
import {
  onAuthStateChanged,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
  updateProfile,
  getAuth,
  signOut,
} from 'firebase/auth';
import {doc, onSnapshot, setDoc} from 'firebase/firestore';

import Landing from '../Landing';
import SignDialog from './SignDialog';
import UserSettingsDialog from './UserSettingsDialog';

export const AuthContext = createContext();

const MESSAGE_MAP = {
  'disconnected': 'firebase',
  'configuration-not-found': 'firebasemail/password',
  'invalid-login-credentials': '',
  'email-already-in-use': '',
  'Missing or insufficient permissions':
    'firestore',
};

const initialState = {
  auth: undefined,
  user: undefined,
  userProps: {
    backgroundColor: '#cccccc',
    avatarDir: '',
    avatar: 'peace',
  },
  authState: 'init',
  error: undefined,
};

/**
 * reducer
 * @param {Object} state state
 * @param {Object} action state
 * @return {Object} state
 */
function reducer(state, action) {
  console.log('authProvider', action, state.error);
  switch (action.type) {
    case 'connect': {
      const a = action.auth;
      if (a) {
        return {
          ...initialState,
          auth: a,
          authState: 'connected',
        };
      } else {
        return {
          ...initialState,
          auth: false,
          authState: 'disconnected',
          error: MESSAGE_MAP['disconnected'],
        };
      }
    }

    case 'authStateChange': {
      const u = action.user;
      if (u) {
        return {
          ...state,
          user: u,
          authState: 'signedIn',
          error: null,
        };
      } else {
        return {
          ...state,
          user: false,
          authState: 'SignDialog:open',
        };
      }
    }

    case 'userPropsChange': {
      const p = action.userProps;
      if (p) {
        return {
          ...state,
          userProps: {
            ...p,
            avatar: 'peace',
          },
          authState: 'ready',
          error: null,
        };
      } else {
        return {
          ...state,
          userProps: {...initialState.userProps},
          authState: 'UserSettingsDialog:open',
        };
      }
    }

    case 'SignDialog:open': {
      return {
        ...state,
        authState: 'SignDialog:open',
        error: null,
      };
    }

    case 'SignDialog:waiting': {
      return {
        ...state,
        authState: 'SignDialog:waiting',
      };
    }

    case 'UserSettingsDialog:open': {
      return {
        ...state,
        authState: 'UserSettingsDialog:open',
        error: null,
      };
    }

    case 'UserSettingsDialog:waiting': {
      return {
        ...state,
        authState: 'UserSettingsDialog:waiting',
      };
    }

    case 'UserSettingsDialog:updated': {
      return {
        ...state,
        userProps: {
          ...action.userProps,
          avatar: 'peace'
        },
        authState: 'UserSettingsDialog:updated',
        error: null,
      };
    }

    case 'error': {
      return {
        ...state,
        error: MESSAGE_MAP[action.errorCode] || action.errorCode,
      };
    }

    default:
      throw new Error(`invalid action ${action.type}`);
  }
}

/**
 *
 * @param {Object} firebase firebase
 * @param {Object} firestore firestore
 * @param {JSX.Element} contectkra
 * @return {JSX.Element} provider
 */
export default function AuthProvider({firebase, firestore, children}) {
  const [state, dispatch] = useReducer(reducer, initialState);
  const unsubscribeRef = useRef();
  const uid = state.user?.uid;

  // -----------------------------------
  // 
  //

  useEffect(() => {
    if (firebase) {
      const auth = getAuth(firebase);
      dispatch({
        type: 'connect',
        auth: auth,
      });

      unsubscribeRef.current = onAuthStateChanged(auth, (user) => {
        console.log('onauthStateChanged', user);
        dispatch({
          type: 'authStateChange',
          user: user,
        });
      });
    }

    return () => {
      if (unsubscribeRef.current) {
        unsubscribeRef.current();
      }
    };
  }, [firebase]);

  // ----------------------------------------------------------
  //  
  //  backgroundColor, avatarDirfirestore
  //  

  useEffect(() => {
    let unsubscribe = null;
    if (uid) {
      const docRef = doc(firestore, 'users', uid);
      unsubscribe = onSnapshot(
        docRef,
        (snap) => {
          if (snap.exists()) {
            const data = snap.data();
            if (
              data.backgroundColor !== state.userProps.backgroundColor ||
              data.avatarDir !== state.userProps.avatarDir
            ) {
              dispatch({
                type: 'userPropsChange',
                userProps: data,
              });
            }
          }
        },
        (error) => {
          dispatch({
            type: 'error',
            errorCode: error.message,
          });
        }
      );
    }

    return () => {
      if (unsubscribe) {
        unsubscribe();
      }
    };
  }, [uid, firestore, state.user, state.userProps]);

  // -----------------------------------------------------------
  //
  // 
  // emailpassword(email
  // )
  //
  /**
   *
   * @param {String} email email
   * @param {String} password password
   */
  function handleSignUp(email, password) {
    dispatch({type: 'SignDialog:waiting'});
    createUserWithEmailAndPassword(state.auth, email, password)
      // onAuthStateChanged
      .then()
      .catch((error) => {
        dispatch({
          type: 'error',
          errorCode: error.message,
        });
      });
  }

  // -----------------------------------------------------------
  //
  // 
  // emailpassword
  //
  /**
   *
   * @param {String} email eail
   * @param {String} password password
   */
  function handleSignIn(email, password) {
    // dispatch({ type: 'SignDialog:waiting' });
    signInWithEmailAndPassword(state.auth, email, password)
      .then((userCredential) => {
        console.log(userCredential);
        dispatch({
          type: 'authStateChange',
          user: userCredential.user,
        });
      })
      // onAuthStateChanged
      .catch((error) => {
        dispatch({
          type: 'error',
          errorCode: error.message,
        });
      });
  }

  // -----------------------------------------------------------
  //
  // 
  //
  /**
   *
   */
  function handleSignOut() {
    dispatch({type: 'SignDialog:waiting'});
    signOut(state.auth);
    // onAuthStateChange
  }

  // -----------------------------------------------------------
  //
  //  
  //
  // (displayName)auth
  // (avatarDir, backgroundColor)firestore
  // firestore
  //
  // users 
  // user                  { id, backgroundColor, avatarDir}
  //     privateLogs 
  //            message  {timestamp, message}

  /**
   *
   * @param {Object} data backgroundColor,avatarDirobj
   */
  function handleChangeUserSettings(data) {
    dispatch({type: 'UserSettingsDialog:waiting'});

    updateProfile(state.auth.currentUser, {displayName: data.displayName})
      .then(() => {
        const docRef = doc(firestore, 'users', uid);
        setDoc(docRef, {
          backgroundColor: data.backgroundColor,
          avatarDir: data.avatarDir,
        })
          .then(() => {
            dispatch({});
            // listernerstate
          })
          .catch((error) => {
            dispatch({type: 'error', errorCode: error.message});
          });
      })
      .catch((error) => {
        dispatch({type: 'error', errorCode: error.message});
      });
  }

  const as = state.authState;
  return (
    <AuthContext.Provider
      value={{
        user: {
          avatar: state.userProps.avatar,
          avatarDir: state.userProps.avatarDir,
          backgroundColor: state.userProps.backgroundColor,
          displayName: state.user?.displayName,
          uid: state.user?.uid,
        },
        uid: state.user?.uid,
        handleSignOut: handleSignOut,
      }}
    >
      {as === 'ready' ? (
        children
      ) : as.startsWith('SignDialog') ? (
        <SignDialog
          authState={state}
          handleSignOut={handleSignOut}
          handleSignUp={handleSignUp}
          handleSignIn={handleSignIn}
        />
      ) : as.startsWith('UserSettingsDialog') ? (
        <UserSettingsDialog
          authState={state}
          handleSignOut={handleSignOut}
          handleChangeUserSettings={handleChangeUserSettings}
        />
      ) : (
        <Landing authState={state} />
      )}
    </AuthContext.Provider>
  );
}
